@charset "UTF-8";

$color-main: #FEB13F;
$color-active: #FFF;
$color-link: #FFF;

@font-face {
  font-family: 'Varela Round';
  src: url("/assets/Varela_Round/VarelaRound-Regular.ttf");
  font-display: swap; }

input, textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0; }

html, body {
  color: #ffffff;
  text-align: center;
  font-family: "Verdana", Helvetica, sans-serif;
  overflow: no-content;
  padding: 0;
  margin: 0; }

html {
  background-color: #63666c;
  /* The image used */
  background-image: url("/assets/water-background-dark-min.png");
  background-attachment: fixed;
  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }
p, ol li {
  max-width: 80vw;
  margin-left: auto;
  margin-right: auto; }
ol li {
  margin-bottom: 10px; }
@media (max-width: 480px) {
  p, ol li {
    max-width: 90vw;
    margin: 10px; } }
.logo {
  font-size: 5em;
  font-family: 'Varela Round', serif;
  margin-bottom: 0; }

.tagline {
  margin: 2em auto;
  font-size: 1.5em; }

.callToAction {
  margin-top: 4em;
  display: flex;
  justify-content: center; }

.button {
  margin: 1rem;
  border: #ffffff 2px solid;
  border-radius: 50px;
  width: 10rem;
  height: 3rem;
  line-height: 3rem;
  font-size: 1.3rem; }

.button-primary {
  background-color: $color-main;
  border-color: $color-main; }

a, a:visited {
  text-decoration: none;
  color: white; }

#mc_embed_signup_scroll {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center; }

#mc_embed_signup {
  margin: auto; }

#mc-embedded-subscribe, #mce-EMAIL {
  border: 0; }

#mce-EMAIL {
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  padding-left: 1em;
  height: 46px;
  width: 17em; }

#mc-embedded-subscribe {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  background-color: $color-main;
  color: #FFF;
  height: 48px; }

$button-height: 27px;
$button-width: 35px;

header {
  background-color: $color-main; }

.container {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  top: 40%;
  left: 0;
  margin: 0 auto;
  font-family: 'Varela Round', sans-serif;

  p {
    font-size: 20px; }

  a {
    display: inline-block;
    position: relative;
    text-align: center;
    color: $color-main;
    text-decoration: none;
    font-size: 20px;
    overflow: hidden;
    top: 5px;

    &:after {
      content: '';
      position: absolute;
      background: $color-main;
      height: 2px;
      width: 0%;
      transform: translateX(-50%);
      left: 50%;
      bottom: 0;
      transition: .35s ease; }

    &:hover:after,
    &:focus:after,
    &:active:after {
      width: 100%; } } }

h1 {
  position: relative;
  text-align: center;
  font-family: 'Varela Round', serif; }

.button_container {
  position: fixed;
  top: 25px;
  right: 15px;
  height: $button-height;
  width: $button-width;
  cursor: pointer;
  z-index: 100;
  transition: opacity .25s ease;

  &:hover {
    opacity: .7; }

  &.active {
    .top {
      transform: translateY(11px) translateX(0) rotate(45deg);
      background: $color-link; }

    .middle {
      opacity: 0;
      background: $color-link; }

    .bottom {
      transform: translateY(-11px) translateX(0) rotate(-45deg);
      background: $color-link; } }

  span {
    background: $color-link;
    border: none;
    height: 5px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .35s ease;
    cursor: pointer;

    &:nth-of-type(2) {
      top: 11px; }

    &:nth-of-type(3) {
      top: 22px; } } }

.overlay {
  position: fixed;
  background: $color-main;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .35s, visibility .35s, height .35s;
  overflow: hidden;

  &.open {
    opacity: .9;
    visibility: visible;
    height: 100%;

    li {
      animation: fadeInRight .5s ease forwards;
      animation-delay: .35s;

      &:nth-of-type(2) {
        animation-delay: .4s; }

      &:nth-of-type(3) {
        animation-delay: .45s; }

      &:nth-of-type(4) {
        animation-delay: .50s; } } }

  nav {
    position: relative;
    height: 70%;
    top: 50%;
    transform: translateY(-50%);
    font-size: 50px;
    font-family: 'Varela Round', serif;
    font-weight: 400;
    text-align: center; }

  ul {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    height: 100%;

    li {
      display: block;
      height: calc(100% / 4);
      min-height: 50px;
      position: relative;
      opacity: 0;

      a {
        display: block;
        position: relative;
        color: $color-link;
        text-decoration: none;
        overflow: hidden;

        &:hover:after,
        &:focus:after,
        &:active:after {
          width: 100%; }

        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 50%;
          width: 0%;
          transform: translateX(-50%);
          height: 3px;
          background: $color-link;
          transition: .35s; } } } } }

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%; }

  100% {
    opacity: 1;
    left: 0; } }

@media screen and (max-width: 420px) {
  html {
    font-size: 12px; } }
